@mixin customFlexAlign($display, $alignItems: none, $justifyContent: none) {
     display: $display;
     align-items: $alignItems;
     justify-content: $justifyContent;
}

@mixin customFont($fontSize, $fontWeight, $fontColor) {
     font-size: $fontSize;
     font-weight: $fontWeight;
     color: $fontColor;
}

@mixin customSize($width, $height) {
     width: $width;
     height: $height;
}

@mixin positionAbsoluteParams($top: 0, $bottom: 0, $left: 0, $right: 0) {
     position: absolute;
     top: $top;
     bottom: $bottom;
     left: $left;
     right: $right;
}